/* eslint-disable no-inner-declarations */
/* eslint-disable prettier/prettier */
/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect } from "react";
import ViewTask from "../View Task/ViewTask";
import { CgCloseR } from "react-icons/cg";

const SelectedMatter = ({ selectedMatter, createTaskData, onCloseSelectedMatter }) => {
  const [loaderSpinner, setloaderSpinner] = useState("");
  const [isSelectedMatter, setIsSelectedMatter] = useState("");
  const [selectedMatterId, setSelectedMatterId] = useState("");
  const [currentMatterId, setCurrentMatterId] = useState("");
  const [SelectedmatterWorkspaceId, setSelectedmatterWorkspaceId] = useState("");
  const [currentMatterWorkspaceId, setCurrentMatterWorkspaceId] = useState("");
  const [currentMilestone, setCurrentMilestone] = useState("");
  const [currentMilestoneId, setCurrentMilestoneId] = useState("");
  const [newAuthenicateToken, setNewAuthenicateToken] = useState("");
  const [taskId, setTaskId] = useState("");
  const [showViewTask, setShowViewTask] = useState(false);

  let baseUrl = "https://app-staging.hivelight.com/";

  let BearerToken;
  let matterId;
  let workspaceId;
  let selectedMatterWorkspaceId;
  let selectedWorkspaceName;
  let workspaceDataArray = [];
  let workspaceCombinedData = [];
  let selectedMilestoneName;
  let selectedMilestoneData;
  let milestoneDataArray = [];
  let milestoneCombinedData = [];
  let authenicateToken;

  useEffect(() => {
    if (selectedMatter) {
      setloaderSpinner(true);
      setIsSelectedMatter(false);
      matterId = selectedMatter.id;
      setSelectedMatterId(matterId);
      localStorage.setItem("matterId", matterId);
      console.log(matterId);

      selectedMatterWorkspaceId = selectedMatter.attributes.workspaceId;
      localStorage.setItem("selectedMatterWorkspaceId", selectedMatterWorkspaceId);
      setSelectedmatterWorkspaceId(selectedMatterWorkspaceId);
      console.log(selectedMatterWorkspaceId);

      selectedMilestoneData = selectedMatter.attributes.milestoneStatus.currentMilestone.name;
      localStorage.setItem("selectedMilestoneData", selectedMilestoneData);
      setCurrentMilestone(selectedMatter.attributes.milestoneStatus.currentMilestone.name);
      console.log(selectedMilestoneData);
    }
  }, [selectedMatter]);

  useEffect(() => {
    if (selectedMatterId) {
      BearerToken = window.localStorage.getItem("Token");
      BearerToken = JSON.parse(BearerToken);
      // console.log(BearerToken);

      selectedMatterWorkspaceId = localStorage.getItem("selectedMatterWorkspaceId");
      console.log(selectedMatterWorkspaceId);

      selectedMilestoneName = localStorage.getItem("selectedMilestoneData");
      console.log(selectedMilestoneName);

      var myHeaders = new Headers();
      myHeaders.append("Authorization", "Bearer " + BearerToken);

      // Fetch All Milestones API Call
      var requestOptions = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };

      fetch(baseUrl + "v1/milestones?matterId=" + selectedMatterId, requestOptions)
        .then((response) => response.json())
        .then((result) => {
          console.log(result);

          const milestoneData = result.data;
          // console.log(milestoneData);

          milestoneDataArray = {
            mileStoneId: milestoneData.map((milestone) => milestone.id),
            mileStoneName: milestoneData.map((milestone) => milestone.attributes.name),
            mileStoneAttributes: milestoneData.map((milestone) => milestone.attributes),
          };
          // console.log(milestoneDataArray);

          milestoneCombinedData = milestoneDataArray.mileStoneId.map((mileStoneId, index) => ({
            mileStoneId: mileStoneId,
            mileStoneName: milestoneDataArray.mileStoneName[index],
            mileStoneAttributes: milestoneDataArray.mileStoneAttributes[index],
          }));
          console.log(milestoneCombinedData);
          localStorage.setItem("milestoneCombinedData", JSON.stringify(milestoneCombinedData));
          const milestoneCombinedDataFromLS = localStorage.getItem("milestoneCombinedData");
          const combinedMilestoneFromLS = JSON.parse(milestoneCombinedDataFromLS);

          const matchingMileStoneName = combinedMilestoneFromLS.filter(
            (mileStone) => mileStone.mileStoneName === selectedMilestoneName
          );
          // console.log(matchingMileStoneName);
          const matterId = matchingMileStoneName[0].mileStoneAttributes["matter-id"];
          localStorage.setItem("matterId", matterId);
          setCurrentMatterId(matterId);
          // console.log(matterId);

          const mileStoneId = matchingMileStoneName[0].mileStoneId;
          setCurrentMilestoneId(mileStoneId);
          // console.log(mileStoneId);
        })
        .catch((error) => {
          console.log("error", error);
        });
    }
  }, [selectedMatterId]);

  useEffect(() => {
    if (currentMatterId) {
      BearerToken = window.localStorage.getItem("Token");
      BearerToken = JSON.parse(BearerToken);

      const selectedMatterWorkspaceId = localStorage.getItem("selectedMatterWorkspaceId");
      console.log(selectedMatterWorkspaceId);

      var myHeaders = new Headers();
      myHeaders.append("Authorization", "Bearer " + BearerToken);

      // whoami API Call
      var requestOptionsData = {
        method: "GET",
        headers: myHeaders,
        redirect: "follow",
      };

      fetch(baseUrl + "v1/users/whoami", requestOptionsData)
        .then((response) => response.json())
        .then((result) => {
          console.log(result);
          let workspaces = result.data.attributes.workspaces;
          // console.log(workspaces);

          workspaceDataArray = {
            workspaceId: workspaces.map((workspace) => workspace.id),
            workspaceNames: workspaces.map((workspace) => workspace.name),
          };
          console.log(workspaceDataArray);

          workspaceCombinedData = workspaceDataArray.workspaceNames.map((workspaceNames, index) => ({
            workspaceId: workspaceDataArray.workspaceId[index],
            workspaceNames: workspaceNames,
          }));
          console.log(workspaceCombinedData);
          localStorage.setItem("workspaceCombinedData", JSON.stringify(workspaceCombinedData));
          const WorkspaceCombinedDataFromLS = localStorage.getItem("workspaceCombinedData");
          const combinedWorkspaceIdFromLS = JSON.parse(WorkspaceCombinedDataFromLS);

          const matchingWorkspaceId = combinedWorkspaceIdFromLS.filter(
            (workspace) => workspace.workspaceId === selectedMatterWorkspaceId
          );
          console.log(matchingWorkspaceId);
          const matterWorkspaceId = matchingWorkspaceId[0].workspaceId;
          setCurrentMatterWorkspaceId(matterWorkspaceId);
          // console.log(matterWorkspaceId);
        })
        .catch((error) => {
          console.log("error", error);
        });
    }
  }, [currentMatterId]);

  useEffect(() => {
    if (currentMatterWorkspaceId) {
      BearerToken = window.localStorage.getItem("Token");
      BearerToken = JSON.parse(BearerToken);

      selectedMatterWorkspaceId = localStorage.getItem("selectedMatterWorkspaceId");
      console.log(selectedMatterWorkspaceId);

      var myHeaders = new Headers();
      myHeaders.append("Authorization", "Bearer " + BearerToken);

      // Authenticate Workspace API Call
      const requestData = {
        meta: {
          token: BearerToken,
          workspaceId: selectedMatterWorkspaceId,
        },
      };

      fetch(baseUrl + "v1/identities/authenticate/workspace", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(requestData),
      })
        .then((response) => {
          if (!response.ok) {
            throw new Error("Network response was not ok");
          }
          return response.json();
        })
        .then((data) => {
          // console.log(data);
          authenicateToken = data.meta.token;
          console.log(authenicateToken);
          setNewAuthenicateToken(authenicateToken);
          setloaderSpinner(false);
          setIsSelectedMatter(true);
        })
        .catch((error) => {
          console.error("There was a problem with the fetch operation:", error);
          setloaderSpinner(false);
          setIsSelectedMatter(true);
        });
    }
  }, [currentMatterWorkspaceId]);

  const cancelMatter = () => {
    onCloseSelectedMatter();
  };

  const createTask = () => {
    // Create TaskObject with initial values
    let TaskObject = {
      BearerToken: newAuthenicateToken,
      matterId: selectedMatterId,
      currentMatterId: currentMatterId,
      currentMatterWorkspaceId: currentMatterWorkspaceId,
      currentMilestoneId: currentMilestoneId,
      subject: "",
      mailBody: "",
    };

    // Retrieve email subject
    const subject = Office.context.mailbox.item.subject;
    TaskObject.subject = subject;

    // Retrieve email body as text
    Office.context.mailbox.item.body.getAsync(Office.CoercionType.Html, function (result) {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        const bodyText = result.value;
        TaskObject.mailBody = bodyText;

        var childDialog;
        Office.context.ui.displayDialogAsync(
          "https://localhost:3000/assets/CreateTask.html",
          { width: 60, height: 80 },
          function (result) {
            if (result.status === Office.AsyncResultStatus.Failed) {
              console.error("Dialog failed to open:", result.error.message);
            } else {
              childDialog = result.value;
              childDialog.addEventHandler(Office.EventType.DialogMessageReceived, function (arg) {
                // Handle messages from the child dialogue
                const messageFromParent = JSON.parse(arg.message);
                console.log("Message from Child:", messageFromParent);

                const closeDialog = messageFromParent;
                setTaskId(closeDialog.taskId);
                const newTaskId = closeDialog.taskId;
                localStorage.setItem("taskId", newTaskId);

                if (newTaskId) {
                  childDialog.close();
                  console.log("Success message received from dialog");

                  setShowViewTask(true);
                  setIsSelectedMatter(false);
                } else if (closeDialog === "error") {
                  console.log("Error message received from dialog");

                  setIsSelectedMatter(true);
                } else if (closeDialog === "Cancel Dialog") {
                  childDialog.close();
                  console.log("Cancel message received from dialog");

                  setIsSelectedMatter(true);
                }
              });

              setTimeout(() => {
                settaskObject();
              }, 3000);
            }
          }
        );

        function settaskObject() {
          // console.log(TaskObject);
          const messageToDialog = JSON.stringify(TaskObject);
          childDialog.messageChild(messageToDialog);
        }
      }
    });
  };

  return (
    <>
      {loaderSpinner ? (
        <>
          <div role="status" className="absolute -translate-x-1/2 -translate-y-1/2 top-2/4 left-1/2">
            <svg
              aria-hidden="true"
              className="w-8 h-8 text-gray-200 animate-spin dark:text-gray-600 fill-blue-950"
              viewBox="0 0 100 101"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                fill="currentColor"
              />
              <path
                d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                fill="currentFill"
              />
            </svg>
            <span className="sr-only">Loading...</span>
          </div>
        </>
      ) : (
        ""
      )}
      {isSelectedMatter ? (
        <div className="overflow-x-hidden rounded-lg mt-2 p-4" style={{ opacity: loaderSpinner ? 0.2 : 1 }}>
          <ul className="mt-4 w-11/12">
            <li className="border p-2 rounded flex flex-row justify-between transition duration-300 ease-in-out hover:bg-blue-200 cursor-pointer bg-[#EBF5FF] focus:outline-none focus:ring focus:ring-blue-200">
              <div>
                <a className="text-blue-500 hover:underline">{selectedMatter.attributes.name}</a>
                <p>
                  {selectedMatter.attributes.statusMessage
                    ? selectedMatter.attributes.statusMessage.message
                    : "No status"}
                </p>
              </div>
              <div onClick={cancelMatter} className="text-lg flex items-center mx-4">
                <CgCloseR />
              </div>
            </li>
          </ul>

          <div className="flex justify-center space-between mt-2">
            <button
              className="cursor-pointer border-[#6F75F3] border-2 text-md py-2 px-4 mt-2 rounded-md hover:bg-[#6F75F3] hover:text-white transition-all"
              onClick={createTask}
            >
              <span className="mr-4 [&>svg]:h-4 [&>svg]:w-4 [&>svg]:text-gray-400 dark:[&>svg]:text-gray-300">
                <i className="fa fa-plus hover:text-white text-sm font-bold" aria-hidden="true"></i>
              </span>
              Create Task
            </button>
          </div>
        </div>
      ) : (
        <></>
      )}

      {showViewTask ? <ViewTask createTask={createTask} /> : <></>}
    </>
  );
};

export default SelectedMatter;
