/* eslint-disable react/prop-types */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-undef */
import React from "react";

const UserDropdown = ({ isUserDropdownOpen }) => {
  let BearerToken;
  const signoutButton = () => {
    window.localStorage.clear("Token");
    BearerToken = window.localStorage.getItem("Token");
    if (BearerToken == "" || BearerToken == null) {
      window.location.reload();
    }
  };

  return (
    <>
      <div
        style={{ position: "absolute", marginLeft: "-110px" }}
        className="text-base list-none bg-white divide-y divide-gray-100 rounded-lg shadow dark:bg-gray-700 dark:divide-gray-600"
        id="user-dropdown"
      >
        <ul className="py-2" aria-labelledby="user-menu-button">
          <li className="hover:border-blue-500 hover:bg-gray-100 p-2 hover:shadow-md hover:text-bold">
            <a
              href=""
              className=" text-sm text-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600 dark:text-gray-200 dark:hover:text-white"
            >
              Profile
            </a>
          </li>
          <li className="hover:border-blue-500 hover:bg-gray-100 p-2 hover:shadow-md hover:text-bold">
            <a
              href=""
              className=" text-sm text-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600 dark:text-gray-200 dark:hover:text-white"
            >
              Manage Organization
            </a>
          </li>
          <li className="hover:border-blue-500 hover:bg-gray-100 p-2 hover:shadow-md hover:text-bold">
            <a
              href=""
              className=" text-sm text-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600 dark:text-gray-200 dark:hover:text-white"
            >
              Ask for Help
            </a>
          </li>
          <li className="hover:border-blue-500 hover:bg-gray-100 p-2 hover:shadow-md hover:text-bold">
            <a
              href=""
              className=" text-sm text-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600 dark:text-gray-200 dark:hover:text-white"
            >
              Support pages
            </a>
          </li>
          <li
            onClick={signoutButton}
            className="hover:border-blue-500 hover:bg-gray-100 p-2 hover:shadow-md hover:text-bold"
          >
            <a className=" text-sm text-gray-700 hover:bg-gray-100 dark:hover:bg-gray-600 dark:text-gray-200 dark:hover:text-white">
              Sign out
            </a>
          </li>
        </ul>
      </div>
    </>
  );
};

export default UserDropdown;
