/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
import React, { useState } from "react";
import SelectedMatter from "./SelectedMatter";

const AllMatter = ({ mattersData, searchMattersData }) => {
  const [selectedMatter, setSelectedMatter] = useState(null);

  const handleSelectedMatterClick = (matter) => {
    setSelectedMatter(matter);
  };

  const handleCancelSearchMatter = () => {
    setSelectedMatter(null);
  };

  return (
    <>
      <>
        {selectedMatter ? (
          <SelectedMatter selectedMatter={selectedMatter} onCloseSelectedMatter={handleCancelSearchMatter} />
        ) : (
          <div className="mt-4 overflow-x-hidden">
            {searchMattersData.length === 0 &&
              mattersData.map((result, index) => (
                <div
                  key={index}
                  className="border-b p-2 m-1 rounded transition duration-300 ease-in-out hover:bg-blue-200 cursor-pointer active:bg-blue-200 focus:outline-none focus:ring focus:ring-blue-200"
                  onClick={() => handleSelectedMatterClick(result)}
                >
                  <a className="text-blue-500 hover:underline">{result.attributes.name}</a>
                  <p>{result.attributes.statusMessage.message}</p>
                </div>
              ))}
          </div>
        )}
      </>
    </>
  );
};

export default AllMatter;
