/* eslint-disable react/prop-types */
/* eslint-disable no-undef */
/* eslint-disable prettier/prettier */
import React, { useState, useEffect, useRef } from "react";
import { jwtDecode } from "jwt-decode";
import AllMatter from "../Matters/AllMatter";
import SearchMatter from "../Matters/SearchMatter";
import SideNavbar from "./SideNavbar";
import UserDropdown from "./UserDropdown";

const Navbar = ({ AllMattersObj }) => {
  const [loaderSpinner, setLoaderSpinner] = useState(false);
  const [isSideNavOpen, setIsSideNavOpen] = useState(false);
  const [mattersData, setmattersData] = useState([]);
  const [searchInput, setSearchInput] = useState("");
  const [isUserDropdownOpen, setIsUserDropdownOpen] = useState(false);
  const [searchResults, setSearchResults] = useState([]);

  const boxRef = useRef(null);
  const menuIconBoxRef = useRef(null);
  console.log(AllMattersObj);

  let baseUrl = "https://app-staging.hivelight.com/";
  Office.onReady(function () {});

  let BearerToken = window.localStorage.getItem("Token");
  BearerToken = JSON.parse(BearerToken);
  // console.log(BearerToken);

  const decodedToken = jwtDecode(BearerToken);
  console.log(decodedToken);

  // Refresh Token Functionality
  const refreshToken = () => {
    // Check if the token is expired or about to expire
    if (decodedToken && decodedToken.exp * 1000 < Date.now() + 300000) {
      const myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      myHeaders.append("Authorization", "Bearer " + BearerToken);

      const requestData = JSON.stringify({
        meta: {
          token: BearerToken,
        },
      });

      const requestOptions = {
        method: "POST",
        headers: myHeaders,
        body: requestData,
        redirect: "follow",
      };

      fetch(baseUrl + "v1/identities/refresh", requestOptions)
        .then((response) => response.text())
        .then((result) => {
          // console.log(result);

          try {
            const parsedData = JSON.parse(result);

            if (parsedData && parsedData.meta && parsedData.meta.token) {
              const refreshToken = parsedData.meta.token;
              console.log("Refresh Token", refreshToken);
              localStorage.setItem("Token", JSON.stringify(refreshToken));
            } else {
              console.error("Invalid JSON structure in the response");
            }
          } catch (error) {
            console.error(error);
          }
        });
    }
  };

  refreshToken();

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (
        menuIconBoxRef.current &&
        !menuIconBoxRef.current.contains(e.target) &&
        boxRef.current &&
        !boxRef.current.contains(e.target)
      ) {
        // Close the navbar
        setIsSideNavOpen(false);
        setIsUserDropdownOpen(false);
      }
    };

    document.addEventListener("click", handleClickOutside);

    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, []);

  const toggleSideNavbar = () => {
    setLoaderSpinner(true);
    setIsSideNavOpen(!isSideNavOpen);
    setIsUserDropdownOpen(false);
    setLoaderSpinner(false);
  };

  const toggleUserDropdown = () => {
    setIsUserDropdownOpen(!isUserDropdownOpen);
    setIsSideNavOpen(false);
  };

  const closeSidebars = () => {
    setIsSideNavOpen(false);
    setIsUserDropdownOpen(false);
  };

  // Fetch the Matter
  useEffect(() => {
    setLoaderSpinner(true);
    let BearerToken = window.localStorage.getItem("Token");
    BearerToken = JSON.parse(BearerToken);
    // console.log(BearerToken);

    var myHeaders = new Headers();
    myHeaders.append("Authorization", "Bearer " + BearerToken);

    var requestOptions = {
      method: "GET",
      headers: myHeaders,
      redirect: "follow",
    };

    fetch(baseUrl + "v1/matters", requestOptions)
      .then((response) => response.json())
      .then((result) => {
        // console.log(result);
        let data = result.data;
        console.log(data);
        localStorage.setItem("allMatters", JSON.stringify(data));

        setmattersData(data);
        setLoaderSpinner(false);
      })
      .catch((error) => {
        console.log("error", error);
        setLoaderSpinner(false);
      });
  }, []);

  const handleSearch = () => {
    setLoaderSpinner(true);
    setIsSideNavOpen(false);
    setIsUserDropdownOpen(false);

    setTimeout(() => {
      const results = mattersData.filter((item) =>
        item.attributes.name.toLowerCase().includes(searchInput.toLowerCase())
      );
      setSearchResults(results);

      if (searchResults) {
        setLoaderSpinner(false);
      } else {
        setLoaderSpinner(false);
      }
    }, 2000);
  };

  return (
    <>
      {loaderSpinner ? (
        <>
          <div role="status" className="absolute -translate-x-1/2 -translate-y-1/2 top-2/4 left-1/2">
            <svg
              aria-hidden="true"
              className="w-8 h-8 text-gray-200 animate-spin dark:text-gray-600 fill-blue-950"
              viewBox="0 0 100 101"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
                fill="currentColor"
              />
              <path
                d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
                fill="currentFill"
              />
            </svg>
            <span className="sr-only">Loading...</span>
          </div>
        </>
      ) : (
        <>
          <div className="sticky top-0" style={{ opacity: loaderSpinner ? 0.2 : 1 }}>
            <nav
              ref={boxRef}
              className="px-1 border-b dark:border-gray-700 border-gray-200 dark:bg-gray-900"
              style={{ backgroundColor: "aliceblue" }}
            >
              <div className="flex justify-center items-center space-x-2">
                <i
                  ref={menuIconBoxRef}
                  onClick={toggleSideNavbar}
                  className="fa fa-bars"
                  aria-hidden="true"
                  style={{ fontSize: "26px", cursor: "pointer" }}
                ></i>
                <input
                  type="text"
                  placeholder="search"
                  value={searchInput}
                  onFocus={() => {
                    closeSidebars();
                  }}
                  onChange={(e) => setSearchInput(e.target.value)}
                  style={{ height: "40px", width: "50%" }}
                  className="px-1 sm:text-xs py-1 bg-white border-2 border-gray-200 focus:border-gray-400 rounded outline-none"
                />
                <button
                  type="button"
                  onClick={handleSearch}
                  className="h-10 mt-2 text-sm relative border mr-4 text-gray-900 bg-white border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium px-5 py-2.5 me-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700"
                  style={{
                    position: "relative",
                    right: "9px",
                    height: "40px",
                  }}
                >
                  Search
                </button>
                <div className="">
                  <button
                    type="button"
                    className="text-sm rounded-full md:me-0 focus:ring-4 focus:ring-gray-300 dark:focus:ring-gray-600"
                    id="user-menu-button"
                    aria-expanded={isUserDropdownOpen}
                    ref={menuIconBoxRef}
                    onClick={toggleUserDropdown}
                  >
                    <span className="sr-only">Open user menu</span>
                    <div className="w-8 h-8 rounded-full bg-white border border-gray-300 text-black flex items-center justify-center">
                      A
                    </div>
                  </button>

                  {isUserDropdownOpen && <UserDropdown isUserDropdownOpen={isUserDropdownOpen} />}
                </div>
              </div>
            </nav>
          </div>
          {isSideNavOpen ? <SideNavbar isSideNavOpen={isSideNavOpen} /> : null}
          {searchResults.length > 0 && <SearchMatter searchMattersData={searchResults} />}
          <AllMatter mattersData={mattersData} searchMattersData={searchResults} />
        </>
      )}
    </>
  );
};

export default Navbar;
