/* eslint-disable no-undef */
/* eslint-disable react/prop-types */
import React, { useState, useEffect } from "react";
import { jwtDecode } from "jwt-decode";

const SideNavbar = ({ isSideNavOpen }) => {
  const [userId, setUserId] = useState("");
  const [currentMatterId, setCurrentMatterId] = useState("");
  const [matterWorkspaceId, setMatterWorkspaceId] = useState("");

  let BearerToken = window.localStorage.getItem("Token");
  // console.log(BearerToken);

  const decoded = jwtDecode(BearerToken);
  console.log(decoded);

  useEffect(() => {
    if (decoded) {
      const selectedUserId = decoded.userId;
      localStorage.setItem("selectedUserId", selectedUserId);
      setUserId(selectedUserId);
    }
  }, [decoded]);

  useEffect(() => {
    if (userId) {
      let selectedMatterWorkspaceId = localStorage.getItem("selectedMatterWorkspaceId");
      console.log(selectedMatterWorkspaceId);
      setMatterWorkspaceId(selectedMatterWorkspaceId);
    }
  }, [userId]);

  useEffect(() => {
    if (matterWorkspaceId) {
      let matterId = localStorage.getItem("matterId");
      console.log(matterId);
      setCurrentMatterId(matterId);
    }
  }, [matterWorkspaceId]);

  const CreateMatter = () => {
    // Create TaskObject with initial values
    let MatterObject = {
      userId: userId,
      BearerToken: BearerToken,
      currentMatterId: currentMatterId,
      matterWorkspaceId: matterWorkspaceId,
    };

    var childDialog;
    Office.context.ui.displayDialogAsync(
      `https://localhost:3000/assets/NewMatter.html?token=${BearerToken}`,
      { width: 70, height: 80 },
      function (result) {
        childDialog = result.value;
        childDialog.addEventHandler(Office.EventType.DialogMessageReceived, function (arg) {
          // Handle messages from the child dialogue
          const messageFromParent = JSON.parse(arg.message);
          console.log("Message from parent:", messageFromParent);
          childDialog.close();

          // setuserId(messageFromParent);
        });

        setTimeout(() => {
          setMatterObject();
        }, 3000);
      }
    );

    function setMatterObject() {
      // console.log(MatterObject);
      const messageToDialog = JSON.stringify(MatterObject);
      childDialog.messageChild(messageToDialog);
    }
  };

  return (
    <>
      <nav
        id="sidenav-5"
        className={`fixed left-0 top-13 z-[1035] h-screen w-60 -translate-x-full overflow-hidden 
            ${isSideNavOpen ? "translate-x-0" : ""} 
            bg-white shadow-[0_4px_12px_0_rgba(0,0,0,0.07),_0_2px_4px_rgba(0,0,0,0.05)] data-[te-sidenav-hidden='false']:translate-x-0 dark:bg-zinc-800`}
        style={{ backgroundColor: "#ebeef1" }}
        data-te-sidenav-init
        data-te-sidenav-hidden="false"
        data-te-sidenav-accordion="true"
      >
        <ul className="relative m-0 list-none px-[0.2rem]" data-te-sidenav-menu-ref>
          <li onClick={CreateMatter} className="relative">
            <a
              className="flex h-12 cursor-pointer items-center truncate rounded-[5px] px-6 py-4 text-[0.875rem] text-gray-600 outline-none transition duration-300 ease-linear hover:bg-slate-50 hover:text-inherit hover:outline-none focus:bg-slate-50 focus:text-inherit focus:outline-none active:bg-slate-50 active:text-inherit active:outline-none data-[te-sidenav-state-active]:text-inherit data-[te-sidenav-state-focus]:outline-none motion-reduce:transition-none dark:text-gray-300 dark:hover:bg-white/10 dark:focus:bg-white/10 dark:active:bg-white/10"
              data-te-sidenav-link-ref
            >
              <span className="mr-4 [&>svg]:h-4 [&>svg]:w-4 [&>svg]:text-gray-400 dark:[&>svg]:text-gray-300">
                <i className="fa fa-plus" aria-hidden="true"></i>
              </span>
              <span>Matter</span>
            </a>
          </li>

          <li className="relative">
            <a
              className="flex h-12 cursor-pointer items-center truncate rounded-[5px] px-6 py-4 text-[0.875rem] text-gray-600 outline-none transition duration-300 ease-linear hover:bg-slate-50 hover:text-inherit hover:outline-none focus:bg-slate-50 focus:text-inherit focus:outline-none active:bg-slate-50 active:text-inherit active:outline-none data-[te-sidenav-state-active]:text-inherit data-[te-sidenav-state-focus]:outline-none motion-reduce:transition-none dark:text-gray-300 dark:hover:bg-white/10 dark:focus:bg-white/10 dark:active:bg-white/10"
              data-te-sidenav-link-ref
            >
              <span className="mr-4 [&>svg]:h-4 [&>svg]:w-4 [&>svg]:text-gray-400 dark:[&>svg]:text-gray-300">
                <i className="fa fa-briefcase" aria-hidden="true"></i>
              </span>
              <span>All Matters</span>
            </a>
          </li>

          <li className="relative">
            <a
              className="flex h-12 cursor-pointer items-center truncate rounded-[5px] px-6 py-4 text-[0.875rem] text-gray-600 outline-none transition duration-300 ease-linear hover:bg-slate-50 hover:text-inherit hover:outline-none focus:bg-slate-50 focus:text-inherit focus:outline-none active:bg-slate-50 active:text-inherit active:outline-none data-[te-sidenav-state-active]:text-inherit data-[te-sidenav-state-focus]:outline-none motion-reduce:transition-none dark:text-gray-300 dark:hover:bg-white/10 dark:focus:bg-white/10 dark:active:bg-white/10"
              data-te-sidenav-link-ref
            >
              <span className="mr-4 [&>svg]:h-4 [&>svg]:w-4 [&>svg]:text-gray-400 dark:[&>svg]:text-gray-300">
                <i className="fa fa-user-circle" aria-hidden="true"></i>
              </span>
              <span>My Matters</span>
            </a>
          </li>
          <li className="relative">
            <a
              className="flex h-12 cursor-pointer items-center truncate rounded-[5px] px-6 py-4 text-[0.875rem] text-gray-600 outline-none transition duration-300 ease-linear hover:bg-slate-50 hover:text-inherit hover:outline-none focus:bg-slate-50 focus:text-inherit focus:outline-none active:bg-slate-50 active:text-inherit active:outline-none data-[te-sidenav-state-active]:text-inherit data-[te-sidenav-state-focus]:outline-none motion-reduce:transition-none dark:text-gray-300 dark:hover:bg-white/10 dark:focus:bg-white/10 dark:active:bg-white/10"
              data-te-sidenav-link-ref
            >
              <span className="mr-4 [&>svg]:h-4 [&>svg]:w-4 [&>svg]:text-gray-400 dark:[&>svg]:text-gray-300">
                <i className="fa fa-star-o" aria-hidden="true"></i>
              </span>
              <span>Stared</span>
            </a>
          </li>
          <li className="relative">
            <a
              className="flex h-12 cursor-pointer items-center truncate rounded-[5px] px-6 py-4 text-[0.875rem] text-gray-600 outline-none transition duration-300 ease-linear hover:bg-slate-50 hover:text-inherit hover:outline-none focus:bg-slate-50 focus:text-inherit focus:outline-none active:bg-slate-50 active:text-inherit active:outline-none data-[te-sidenav-state-active]:text-inherit data-[te-sidenav-state-focus]:outline-none motion-reduce:transition-none dark:text-gray-300 dark:hover:bg-white/10 dark:focus:bg-white/10 dark:active:bg-white/10"
              data-te-sidenav-link-ref
            >
              <span className="mr-4 [&>svg]:h-4 [&>svg]:w-4 [&>svg]:text-gray-400 dark:[&>svg]:text-gray-300">
                <i className="fa fa-archive" aria-hidden="true"></i>
              </span>
              <span>Archived</span>
            </a>
          </li>
        </ul>
      </nav>
    </>
  );
};

export default SideNavbar;
